<template>
<div>
  <h2>Contact details</h2>
<el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="ruleForm slideUp">
  <el-form-item prop="name">
    <el-input v-model="ruleForm.name" placeholder="Name"></el-input>
  </el-form-item>
  <el-form-item prop="email">
    <el-input v-model="ruleForm.email" placeholder="Email"></el-input>
  </el-form-item>
  <el-form-item prop="phone">
    <el-input v-model.number="ruleForm.phone" placeholder="Phone"></el-input>
  </el-form-item>
    <el-form-item>
    <el-select v-model="ruleForm.job" clearable filterable placeholder="Jobs">
    <el-option
      v-for="item in ruleForm.jobs"
      :key="item.category"
      :value="item.category">
    </el-option>
  </el-select>
  </el-form-item>
  <el-form-item prop="checked">
    <el-checkbox label="I consent to having Besttoolbars collect my personal details." v-model="ruleForm.checked" name="details"></el-checkbox>
  </el-form-item>
  <el-form-item prop="desc">
    <el-input type="textarea" v-model="ruleForm.desc" placeholder="Write your optional cover letter here."></el-input>
  </el-form-item>
  <el-form-item>
  <el-upload
  action="https://jsonplaceholder.typicode.com/posts/"
  class="upload"
  ref="upload"
  :auto-upload="false">
  <el-button slot="trigger" type="text">Attach file</el-button>
</el-upload>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="submitForm('ruleForm')">Talk to our team</el-button>
    <el-button @click="resetForm('ruleForm')">Reset</el-button>
  </el-form-item>
</el-form>
</div>
</template>

<script>
  export default {
    data() {
      return {
        ruleForm: {
          name: '',
          email: '',
          phone: '',
          jobs: [{
          category: 'Middle iOS Developer'
        }, {
          category: 'Middle Android Developer'
        }, {
          category: 'Junior iOS Developer'
        }, {
          category: 'Junior Android Developer'
        }, {
          category: 'Senior iOS Developer'
        }, {
          category: 'Senior Android Developer'
        }],
          checked: [],
          desc: '',
          value: '',
          job: ''
        },
        rules: {
          name: [
            { required: true, message: 'Please input your name', trigger: 'blur' }
          ],
          email: [
            { required: true, message: 'Please input email address', trigger: 'blur' },
            { type: 'email', message: 'Please input correct email address', trigger: ['blur', 'change'] }
          ],
          phone: [
            { required: true, message: 'Please input phone number'},
            { type: 'number', message: 'Phone must be a number'}
          ],
          checked: [
            { type: 'array', required: true, message: 'Please сonfirm your consent to the processing of personal data', trigger: 'change' }
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      submitUpload() {
        this.$refs.upload.submit();
      }
    },
  }
</script>
